<template>
  <div class="sectionone">
    <div class="lunbo">
      <van-swipe :autoplay="2000" indicator-color="white">
        <van-swipe-item>
          <img src="./img/lunboutu_1@2x.png" alt />
        </van-swipe-item>
        <van-swipe-item>
          <img src="./img/banner1.png" alt />
        </van-swipe-item>
        <van-swipe-item>
          <img src="./img/banner2.png" alt />
        </van-swipe-item>
        <van-swipe-item>
          <img src="./img/banner3@2x.png" alt />
        </van-swipe-item>
      </van-swipe>
    </div>

    <div class="choadr">
      <h1>选择城市地点</h1>
      <div class="nowadr">
        <div class="left">
          <p class="shi" @click="tiaofn">郑州</p>
          <p class="diqu">金水区关虎屯</p>
        </div>
        <div class="right">
          <!-- <img src="./img/anniu_1@2x.png" alt /> -->
          <van-switch
            v-model="checked"
            active-color="#FCE969"
            inactive-color="#f0f0f0"
            size="0.3rem"
          />
          <p v-text="checktext">异地还车</p>
        </div>
      </div>
      <div class="targetadr">
        <p @click="tiaofn">北京</p>
        <p>朝阳区</p>
      </div>
      <div class="chotime">
        <div class="left" @click="showPopup1">
          <p>{{MM1}}月{{DD1}}日</p>
          <p>
            <span>{{xx1}}</span>
            <span>{{hh1}}：{{mm1}}</span>
          </p>
        </div>
        <div class="midle">
          <p>{{days}}天</p>
          <img src="./img/jindutiao_1@2x.png" alt />
        </div>
        <div class="right" @click="showPopup2">
          <p>{{MM2}}月{{DD2}}日</p>
          <p>
            <span>{{xx2}}</span>
            <span>{{hh2}}：{{mm2}}</span>
          </p>
        </div>
      </div>
    </div>

    <div class="chocar">
      <h1>选择车型</h1>
      <ul class="carnav">
        <li>
          <router-link to="/indexgen/indexone/dazhong">大众</router-link>
        </li>
        <li>
          <router-link to="/indexgen/indexone/aodi">奥迪</router-link>
        </li>
        <li>
          <router-link to="/indexgen/indexone/jiebao">捷豹</router-link>
        </li>
        <li>
          <router-link to="/indexgen/indexone/benchi">奔驰</router-link>
        </li>
        <li>
          <router-link to="/indexgen/indexone/baoma">宝马</router-link>
        </li>
        <li>
          <router-link to="/more">更多</router-link>
        </li>
      </ul>
      <router-view></router-view>
    </div>
    <div class="carhome">
      <div class="footbox">
        <div class="left">
          <input type="checkbox" checked />
          <span>送车到家</span>
        </div>
        <div class="right">
          <input type="checkbox" />
          <span>上门取车</span>
        </div>
      </div>
      <button @click="tiaodingdan()">立即租车</button>
    </div>
    <van-popup v-model="show1" position="bottom" :style="{ height: '40%' }">
      <van-datetime-picker
        v-model="currentDate1"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="queren"
        @cancel="quxiao"
      />
    </van-popup>
    <van-popup v-model="show2" position="bottom" :style="{ height: '40%' }">
      <van-datetime-picker
        v-model="currentDate2"
        type="datetime"
        :min-date="minDate"
        :max-date="maxDate"
        @confirm="queren"
        @cancel="quxiao"
      />
    </van-popup>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      checked: true,
      checktext: "异地还车",
      show1: false,
      show2: false,
      minHour: 0,
      maxHour: 24,
      minDate: new Date(),
      maxDate: new Date(2019, 11, 31),
      currentDate1: new Date(),
      currentDate2: new Date(),
      MM1: 0,
      DD1: 0,
      hh1: 0,
      mm1: 0,
      xx1: "",
      MM2: 0,
      DD2: 0,
      hh2: 0,
      mm2: 0,
      xx2: "",
      days: 0
    };
  },
  beforeCreate() {
    this.$router.push({ path: "/indexgen/indexone/dazhong" });
  },
  mounted() {
    localStorage.setItem("useringtime", 0);
    let xarr = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];
    let ri1 = this.currentDate1;
    this.MM1 =
      ri1.getMonth() - 0 + 1 < 10
        ? "0" + (ri1.getMonth() - 0 + 1)
        : ri1.getMonth() - 0 + 1;
    this.DD1 = ri1.getDate() < 10 ? "0" + ri1.getDate() : ri1.getDate();
    this.hh1 = ri1.getHours() < 10 ? "0" + ri1.getHours() : ri1.getHours();
    this.mm1 =
      ri1.getMinutes() < 10 ? "0" + ri1.getMinutes() : ri1.getMinutes();
    this.xx1 = xarr[ri1.getDay()];

    let ri2 = this.currentDate2;
    this.MM2 =
      ri2.getMonth() - 0 + 1 < 10
        ? "0" + (ri2.getMonth() - 0 + 1)
        : ri2.getMonth() - 0 + 1;
    this.DD2 = ri2.getDate() < 10 ? "0" + ri2.getDate() : ri2.getDate();
    this.hh2 = ri2.getHours() < 10 ? "0" + ri2.getHours() : ri2.getHours();
    this.mm2 =
      ri2.getMinutes() < 10 ? "0" + ri2.getMinutes() : ri2.getMinutes();
    this.xx2 = xarr[ri2.getDay()];


  },
  methods: {
    tiaofn() {
      this.$router.push({ path: "/city" });
    },
    tiaodingdan() {
      this.$toast("快捷租车正在维护,请选择更多车型");

    },
    showPopup1() {
      this.show1 = true;
    },
    showPopup2() {
      this.show2 = true;
    },
    queren() {
      let xarr = ["周日", "周一", "周二", "周三", "周四", "周五", "周六"];
      let ri1 = this.currentDate1;
      this.MM1 =
        ri1.getMonth() - 0 + 1 < 10
          ? "0" + (ri1.getMonth() - 0 + 1)
          : ri1.getMonth() - 0 + 1;
      this.DD1 = ri1.getDate() < 10 ? "0" + ri1.getDate() : ri1.getDate();
      this.hh1 = ri1.getHours() < 10 ? "0" + ri1.getHours() : ri1.getHours();
      this.mm1 =
        ri1.getMinutes() < 10 ? "0" + ri1.getMinutes() : ri1.getMinutes();
      this.xx1 = xarr[ri1.getDay()];

      let ri2 = this.currentDate2;
      this.MM2 =
        ri2.getMonth() - 0 + 1 < 10
          ? "0" + (ri2.getMonth() - 0 + 1)
          : ri2.getMonth() - 0 + 1;
      this.DD2 = ri2.getDate() < 10 ? "0" + ri2.getDate() : ri2.getDate();
      this.hh2 = ri2.getHours() < 10 ? "0" + ri2.getHours() : ri2.getHours();
      this.mm2 =
        ri2.getMinutes() < 10 ? "0" + ri2.getMinutes() : ri2.getMinutes();
      this.xx2 = xarr[ri2.getDay()];

      this.show1 = false;
      this.show2 = false;

      //  http://172.25.7.31:8080/orders/getTime?b_time=2019-02-23%2012:23:23&e_time=2019-02-19%2013:25:23
      let btime =
        "2019-" +
        this.MM1 +
        "-" +
        this.DD1 +
        " " +
        this.hh1 +
        ":" +
        this.mm1 +
        ":00";
      let etime =
        "2019-" +
        this.MM2 +
        "-" +
        this.DD2 +
        " " +
        this.hh2 +
        ":" +
        this.mm2 +
        ":00";
      // console.log(btime);
      // console.log(etime);
      this.$axios
        .get(
          "http://192.168.0.112:8080/counts/getTime?b_time=" +
            btime +
            "&e_time=" +
            etime
        )
        .then(op => {
          //  console.log(op.data);
          localStorage.setItem("useringtime", op.data);
          localStorage.setItem("userendtime", etime);
          this.days = (op.data / 24).toFixed(2);
        })
        .catch(err => {
          console.log(err);
        });
    },
    quxiao() {
      this.show1 = false;
      this.show2 = false;
    }
  },
  updated() {
    if (this.checked) {
      this.checktext = "异地还车";
    } else {
      this.checktext = "本地还车";
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.sectionone {
  padding-top: 1.56rem;
  background-color: #f1f1f1;
  padding-bottom: 0.4rem;
  .lunbo {
    width: 100%;
    height: 3.39rem;
    img {
      width: 100%;
      height: 3.39rem;
    }
  }
  .choadr {
    text-align: left;
    padding: 0.3rem;
    width: 6.9rem;
    // height: 4rem;
    background-color: #fff;
    position: absolute;
    left: 0.3rem;
    top: 4.38rem;
    border-radius: 0.15rem;

    h1 {
      font-size: 0.22rem;
      color: #000;
    }
    .nowadr {
      display: flex;
      justify-content: space-between;
      padding: 0.42rem 0 0.21rem;
      border-bottom: 0.02rem solid #e1e1e1;
      .left {
        p {
          font-size: 0.3rem;
          margin-right: 0.35rem;
        }

        display: flex;
      }
      .right {
        display: flex;
        img {
          width: 0.64rem;
          height: 0.42rem;
        }

        p {
          font-size: 0.22rem;
          margin-left: 0.27rem;
        }
      }
    }
    .targetadr {
      display: flex;
      height: 0.86rem;
      line-height: 0.86rem;
      border-bottom: 0.02rem solid #e1e1e1;
      color: #333;
      p {
        height: 100%;
        font-size: 0.3rem;
        margin-right: 0.35rem;
      }
    }
    .chotime {
      font-size: 0.3rem;
      display: flex;
      justify-content: space-between;
      padding-top: 0.3rem;
      margin-bottom: 0.1rem;
      p {
        span {
          font-size: 0.22rem;
          color: #999;
        }
      }
      .left {
        text-align: left;
      }
      .midle {
        text-align: center;
        img {
          width: 1.75rem;
        }
      }
      .right {
        text-align: right;
      }
    }
  }

  .chocar {
    width: 6.9rem;
    min-height: 7.7rem;
    margin: 3.82rem auto 0;
    background-color: #fff;
    border-radius: 0.15rem;
    padding: 0.3rem;
    h1 {
      font-size: 0.22rem;
      text-align: left;
    }
    .carnav {
      display: flex;
      font-size: 0.24rem;
      padding: 0.4rem 0.1rem 0;
      justify-content: space-between;
      border-bottom: 0.02rem solid #e1e1e1;
      li {
        // border-bottom: 0.04rem solid #ffe009;
        a {
          padding-bottom: 0.1rem;
          display: block;
          border-bottom: 0.04rem solid #fff;
          color: #666;
        }
        .router-link-active {
          border-bottom: 0.04rem solid #ffe009;
        }
      }
    }
  }
  .carhome {
    width: 6.9rem;
    height: 2.14rem;
    font-size: 0;
    background-color: #fff;
    border-radius: 0.15rem;
    margin: 0.4rem auto 0;
    padding: 0.3rem;
    .footbox {
      display: flex;
      font-size: 0.24rem;
      color: #666;
      justify-content: space-between;
      input {
        width: 0.32rem;
        height: 0.32rem;
        border: 0.02rem solid #ccc;
        margin-right: 0.16rem;
      }
      .left,
      .right {
        display: flex;
        align-items: center;
      }
    }
    button {
      width: 6.24rem;
      height: 0.6rem;
      margin-top: 0.36rem;
      background-color: #ffe009;
      color: #333;
      font-size: 0.26rem;
    }
  }
}
</style>



