<template>
  <div class="xiangqingbox">
    <Headtwo title="订单信息"></Headtwo>
    <ul class="list">
      <li class="one">
        <!-- <img src="../../../static/upload/baoma525.jpg" /> -->
        <img :src="imgsrc" />
        <div class="info">
          <h1>{{carname}}</h1>
          <p class="tedian">
            <van-tag>经济</van-tag>
            <van-tag>方便</van-tag>
            <van-tag>快捷</van-tag>
          </p>
          <div>
            <van-tag plain>车型配置</van-tag>
            <span class="price">￥{{carprice}}</span>/小时
          </div>
        </div>
      </li>
      <li class="two">
        <p>
          取车地点:
          <span @click="tiaomenxiang()">河南省郑州市金水区兰德中心店</span>
        </p>
        <p>
          还车地点:
          <span>河南省洛阳市皇城公园店</span>
        </p>
        <p>
          还车时间:
          <span>{{userendtime}}</span>
        </p>
        <p>
          租车总金额:
          <span>{{useringtime}}小时×￥{{carprice}}/小时={{useringtime*carprice}}元</span>
        </p>
      </li>
      <li class="three">
        <h1>支付方式</h1>
        <div class="pbox">
          <img src="./img/yuezhifu.png" alt />
          <span>余额支付</span>
        </div>
        <div class="pbox">
          <img src="./img/weixinzhifu.png" alt />
          <span>微信支付</span>
        </div>
        <div class="pbox">
          <img src="./img/zhifubaozhifu.png" alt />
          <span>支付宝支付</span>
        </div>
        <div class="radiobox">
          <van-radio-group v-model="radio">
            <van-radio name="余额" checked-color="#ffe009"></van-radio>
            <van-radio name="微信" checked-color="#ffe009"></van-radio>
            <van-radio name="支付宝" checked-color="#ffe009"></van-radio>
          </van-radio-group>
        </div>
      </li>
      <li class="frou">
        <h1>
          <van-checkbox v-model="checked" checked-color="#ffe009">同意条款</van-checkbox>
        </h1>
        <input type="button" value="立即支付" @click="zhifufn()" />
      </li>
    </ul>
    <van-popup v-model="show" :overlay="noshow">
      <van-loading size="24px" vertical type="spinner">{{radio}}正在支付中</van-loading>
    </van-popup>
  </div>
</template>

<script>
import Headtwo from "@/components/head/headtwo";
export default {
  props: {},
  data() {
    return {
      radio: "余额",
      checked: true,
      show: false,
      noshow: false,
      carname: "丰田汉兰达",
      carprice: 68,
      imgsrc: "./img/chepic.png",
      userendtime: "2019-06-05",
      useringtime: 0,
      allmoney: 0
    };
  },
  mounted() {
    let cararr = JSON.parse(localStorage.getItem("nowlodingcar"));
    this.carname = cararr.ctype + cararr.cname;
    this.carprice = cararr.h_price;
    this.imgsrc = cararr.images;
    this.userendtime = localStorage.getItem("userendtime");
    this.useringtime = localStorage.getItem("useringtime");

    localStorage.removeItem('cardinginfo');
  },
  methods: {
    tiaomenxiang() {
      this.$router.push("/menxiang");
      
    },
    zhifufn() {
      let username = localStorage.getItem("username");
      if (username) {
       
        if(this.useringtime==0){
          this.$toast("请选择租车时间");
       }else if (!this.checked) {
          this.$toast("请先同意条款");
        } else if (this.radio == "微信") {
          this.$toast("微信支付正在维护");
        } else if (this.radio == "支付宝") {
          this.$toast("支付宝正在维护");
        } else {

          let timer = null;
          
          let nowpay = this.carprice * this.useringtime;
          this.$axios
            .get("http://192.168.0.112:8080/counts/yue?uname=" + username)
            .then(op => {
              this.allmoney = op.data;
              if (nowpay > op.data) {
                this.$toast("您的余额不足请先充值");
                // console.log(op.data);
              } else {
                 this.show = true;
                this.$axios
                  .get(
                    "http://192.168.0.112:8080/counts/chongzhi?uname=" +
                      username +
                      "&czmoney=-" +
                      nowpay
                  )
                  .then(op => {
                    if (op.data == "success") {
                      clearInterval(timer);
                      timer = setTimeout(() => {
                        this.show = false;
                         this.$toast("您的订单已支付成功");
                         
                        console.log(this.carname);
                        console.log(this.imgsrc);
                        console.log(this.userendtime);
                        console.log(nowpay);
                          let obj ={
                             name:this.carname,
                             imgsrc:this.imgsrc,
                             endtime:this.userendtime,
                             price:nowpay
                          }
                          localStorage.setItem('cardinginfo',JSON.stringify(obj));

                         setTimeout(()=>{
                            this.$router.push('/indexgen');
                         },1000)
                      }, 3000);
                    
                    } else {
                      this.$toast("支付失败");
                    }
                  });
              }
            })
            .catch(err => {
              console.log(err);
            });
        }
     
      }else {
          this.$toast("请先登录");
          }
    }
  },
  updated() {
   
  },
  components: {
    Headtwo
  }
};
</script>

<style scoped lang="less">
.xiangqingbox {
  padding-top: 0.88rem;
  height: 100%;
  background-color: #f0f0f0;
  .van-loading {
    width: 3rem;
    border: #ccc 0.02rem solid;
    padding: 0.3rem;
    border-radius: 0.1rem;
  }
}
.list {
  padding: 0.3rem 0 0 0;
  background-color: #f0f0f0;
  li {
    position: relative;
    background: #fff;
    margin: 0 0.3rem;
    border-radius: 0.16rem;
    font-size: 0;
    text-align: left;
  }
  .one {
    height: 2.33rem;
    display: flex;
    align-items: center;
    margin-bottom: 0.3rem;
    img {
      width: 2.33rem;
      height: 1.39rem;
      margin: 0 0.5rem 0 0.36rem;
    }
    .info {
      h1 {
        font-size: 0.32rem;
      }
      .tedian {
        height: 0.36rem;
        margin: 0.24rem 0 0.28rem;
        span {
          padding: 0;
        }
        .van-tag {
          margin-right: 0.1rem;
          background-color: #ccc;
          padding: 0 0.1rem;
        }
      }
      div {
        font-size: 0.24rem;
        .price {
          font-size: 0.32rem;
          color: red;
        }
      }
    }
  }
  .two {
    height: 2.85rem;
    font-size: 0.24rem;
    color: #999;
    padding-top: 0.3rem;
    margin-bottom: 0.3rem;
    p {
      // margin-top: 0.36rem;
      line-height: 0.6rem;
      margin-left: 0.65rem;
      position: relative;
      span {
        position: absolute;
        top: 0;
        left: 1.6rem;
      }
    }
  }
  .three {
    height: 3.79rem;
    padding: 0.35rem 0.55rem;
    font-size: 0.3rem;
    position: relative;
    margin-bottom: 0.3rem;
    .pbox {
      display: flex;
      align-items: center;
      // padding-top: 0.48rem;
      line-height: 0.94rem;

      img {
        width: 0.36rem;
        height: 0.31rem;
        margin: 0 0.2rem 0 0.8rem;
      }
    }
    .radiobox {
      position: absolute;
      top: 1.02rem;
      left: 0.6rem;
      // height: 2.2rem;
      .van-radio {
        margin-bottom: 0.54rem;
        //  line-height: 0.94rem;
      }
    }
  }
  .frou {
    height: 1.9rem;
    font-size: 0.22rem;

    padding: 0.24rem 0.58rem;
    span {
      color: #ccc;
    }
    input {
      width: 100%;
      height: 0.65rem;
      margin: 0 auto;
      color: #333;
      border: none;
      outline: none;
      background-color: #ffe009;
      border-radius: 0.1rem;
      margin-top: 0.4rem;
    }
  }
}
</style>



