<template>
  <div class="carlistbox">
    <Headtwo title="车型列表" rgt=" "></Headtwo>
    <div class="navlist">
      <van-dropdown-menu class="navlist2">
        <van-dropdown-item v-model="value1" :options="option1" />
        <van-dropdown-item v-model="value2" :options="option2" />
        <van-dropdown-item v-model="value3" :options="option3" />
        <van-dropdown-item v-model="value4" :options="option4" />
      </van-dropdown-menu>
    </div>
    <ul class="carlist">
      <li v-for="(item,index) of carlists" :key="index" @click="tiaofn(index)">
        <img :src="'./'+item.images" />
        <div class="one">
          <div class="carname" v-text="item.cname"></div>
          <P>
            <span v-text="item.h_price"></span>/小时
          </P>
        </div>
        <div class="two">
          <span>{{item.cDesc.split('，')[2]}}</span>
          <span>{{item.cDesc.split('，')[0]}}</span>
          <span>{{item.cDesc.split('，')[1]}}</span>
          <!-- v-text="(item.cc)" -->
          <!-- <img src="./img/che_3@2x.png" alt=""> -->
        </div>
      </li>

      <!-- <li>
        <img src="../img/upload/dazhongjieda.jpg" alt />
        <div class="one">
          <div class="carname">奥迪A6</div>
          <P>
            <span>85元</span>/小时
          </P>
        </div>
        <div class="two">
          <span>SUV</span>
          <span>2.7自动</span>
          <span>乘坐5人</span>
        </div>
      </li>-->
    </ul>
  </div>
</template>

<script>
import Headtwo from "@/components/head/headtwo";
export default {
  props: {},
  data() {
    return {
      a: "",
      maxb: "",
      minb: "",
      c: "",
      d: "",
      carlists: [
        {
          cid: 1,
          ctype: "宝马",
          cname: "525",
          cc: "2.5T",
          boxNum: 3,
          peopleNum: 5,
          cDesc: "轿车，性能好，舒适型",
          h_price: 98,
          d_price: 668,
          cNum: "豫A50A25",
          cStatus: 0,
          images: "./static/upload/baoma525.jpg"
        }
      ],
      value1: "不限",
      value2: "不限",
      value3: "不限",
      value4: "不限",
      option1: [
        { text: "不限", value: "不限" },
        { text: "郑州", value: "郑州" },
        { text: "北京", value: "北京" },
        { text: "上海", value: "上海" }
      ],
      option2: [
        { text: "不限", value: "不限" },
        { text: "0 - 30", value: "0 - 30" },
        { text: "30 - 50", value: "30 - 50" },
        { text: "50 - 80", value: "50 - 80" },
        { text: "80 - 100", value: "80 - 100" },
        { text: "100 - 1000", value: "100 - 1000" },
        { text: "1000 - 3000", value: "1000 - 3000" }
      ],
      option3: [
        { text: "不限", value: "不限" },
        { text: "大众", value: "大众" },
        { text: "宝马", value: "宝马" },
        { text: "奥迪", value: "奥迪" },
        { text: "保时捷", value: "保时捷" },
        { text: "本田", value: "本田" },
        { text: "宝骏", value: "宝骏" },
        { text: "别克", value: "别克" }
      ],
      option4: [
        { text: "不限", value: "不限" },
        { text: "suv", value: "suv" },
        { text: "轿车", value: "轿车" },
        { text: "跑车", value: "跑车" },
        { text: "mpv", value: "mpv" },
        { text: "经济型", value: "经济型" },
        { text: "舒适型", value: "舒适型" },
        { text: "豪华型", value: "豪华型" }
      ]
    };
  },
  methods: {
    tiaofn(index) {
      // console.log(this.carlists[index])
       localStorage.setItem('nowlodingcar',JSON.stringify(this.carlists[index]));
      this.$router.push("/chexiang");
    }
  },
  mounted() {
    // getAll
    //     getCtype?ctype=?  根据品牌模糊查询
    // getCNum?cNum=?   根据车牌精确查找
    // getCDesc?cDesc=?   根据标签特点模糊查询
    // getById?cid=?    根据id精确查找
    // getByH_price?min_price=?&max_price=?    根据价格区间查询
    // getByASC      时租价升序查询
    // getByDESC      日组价降序查询
    // getByCityCtype?city=郑州&ctype=大

    
    this.$axios
      .get('http://192.168.0.112:8080/cars/getByFour?city=&ctype=&cDesc=&min_price=&max_price=')
      .then(op => {
        this.carlists = op.data;
      })
      .catch(err => {
        console.log(err);
      });
  },
  computed: {},
  updated() {
    if (this.value1 == "不限") {
      this.a = "";
    } else {
      this.a = this.value1;
    }
    if (this.value2 == "不限") {
        this.maxb = "";
       this.minb = "";
    } else {
      // console.log(this.value2.split(' '));
      this.minb = this.value2.split(' ')[0];
      this.maxb = this.value2.split(' ')[2];
    }
    if (this.value3 == "不限") {
      this.c = "";
    } else {
      this.c = this.value3;
    }
    if (this.value4 == "不限") {
      this.d = "";
    } else {
      this.d = this.value4;
    }
    // console.log(this.a);
    // console.log( this.maxb);
    // console.log( this.minb);
    // console.log(this.c);
    // console.log(this.d);
    this.$axios
      .get(
        "http://192.168.0.112:8080/cars/getByFour?city="+this.a+"&ctype="+this.c+"&cDesc="+this.d+"&min_price="+this.minb+"&max_price="+this.maxb
      )
      .then(op => {
        // console.log(op);
        this.carlists = op.data;
      })
      .catch(err => {
        console.log(err);
      });
  },
  components: {
    Headtwo
  }
};
</script>

<style scoped lang="less">
.navlist2 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 0.8rem;
  font-size: 0.3rem;
}
.navlist2 > div {
  flex: 1;
  margin-right: 0.8rem;
  width: 100%;
}
.carlistbox {
  font-size: 0.24rem;
  overflow: hidden;
  background-color: #f0f0f0;
  height: 100%;
}
.navlist {
  height: 0.8rem;
  line-height: 0.8rem;
  margin-top: 0.88rem;
  padding: 0 0.48rem;
  display: flex;
  justify-content: space-between;
  background-color: #fff;
  select {
    border: solid 1px #fff;
    appearance: none;
    padding-right: 14px;
    background: url("./img/fanhui_2@2x.png") no-repeat scroll right center
      transparent;
    background-size: 0.15rem 0.24rem;
    option::-ms-expand {
      display: none;
    }
    option {
      -moz-appearance: none; /* Firefox */
      -webkit-appearance: none; /* Safari 和 Chrome */
      appearance: none;
    }
    /* --背景色字体颜色--*/
    option:hover {
      color: rgb(67, 38, 228);
      background-color: #e07130;
    }
  }
}
.carlist {
  max-height: 11.2rem;
  overflow-y: auto;
  margin: 0.2rem 0.3rem 0;
  background-color: #fff;
  display: flex;
  flex-wrap: wrap;
  border-radius: 0.1rem;
  li {
    width: 3rem;
    margin-left: 0.3rem;

    img {
      width: 2.7rem;
      height: 1.65rem;
      margin: 0.43rem auto 0.47rem;
    }
    .one {
      display: flex;
      justify-content: space-between;
      width: 2.7rem;
      .carname {
        color: #333;
        font-size: 0.3rem;
      }
      p {
        color: #999;
        font-size: 0.2rem;
        span {
          color: red;
          font-size: 0.3rem;
        }
      }
    }
    .two {
      margin-top: 0.24rem;
      margin-bottom: 0.12rem;
      text-align: left;
      span {
        font-size: 0.18rem;
        color: #666;
        padding: 0.05rem 0.1rem;
        background-color: #f0f0f0;
        border-radius: 0.05rem;
      }
    }
  }
}
</style>



