<template>
  <div class="chongzhi">
    <Headtwo title="充值" rgt=" "></Headtwo>
    <!--我的余额-->
    <section class="my-balance">
      <div class="my-balancebox">
        <div class="left">
          <img src="./img/recharge/jinbi_1@2x.png" alt />
        </div>
        <div class="right">
          <span>我的余额：{{allmoney}}元</span>
          <p>我当前的余额</p>
        </div>
      </div>
      <div class="select">
        <p>选择金额</p>
        <ul class="moneylist">
          <li class="oneliqian">100</li>
          <li>200</li>
          <li>300</li>
          <li>500</li>
          <li>600</li>
          <li>800</li>
          <li>1000</li>
          <li>3000</li>
        </ul>
      </div>
    </section>
    <!--支付方式-->
    <section class="way">
      <p>支付方式</p>
      <ul>
        <li>
          <img src="./img/recharge/weixin_1@2x.png" />
          <span class="txt">微信</span>
          <span class="img">
            <!-- <img src="./img/recharge/xuanze_1@2x.png" /> -->
            <!-- <img src="./img/recharge/xuanze_2@2x.png" > -->
          </span>
        </li>
        <li>
          <img src="./img/recharge/yinlian_1@2x.png" />
          <span class="txt">银联</span>
          <span class="img">
            <!-- <img src="./img/recharge/xuanze_1@2x.png" class="nomal" /> -->
            <!-- <img src="./img/recharge/xuanze_2@2x.png" class="active"> -->
          </span>
        </li>
        <li>
          <img src="./img/recharge/zhifubao_1@2x.png" />
          <span class="txt">支付宝</span>
          <span class="img">
            <!-- <img src="./img/recharge/xuanze_1@2x.png" class="nomal"> -->
            <!-- <img src="./img/recharge/xuanze_2@2x.png" class="active" /> -->
          </span>
        </li>
      </ul>
      <div class="danxuan">
        <van-radio-group v-model="radio">
          <van-radio name="微信" checked-color="#ffe009"></van-radio>
          <van-radio name="银联" checked-color="#ffe009"></van-radio>
          <van-radio name="支付宝" checked-color="#ffe009"></van-radio>
        </van-radio-group>
      </div>
      <div class="btn" @click="chongzhifn()">立即充值</div>
    </section>
    <van-popup v-model="show" :overlay="noshow">
      <van-loading size="24px" vertical type="spinner">{{radio}}正在支付中</van-loading>
    </van-popup>
  </div>
</template>

<script>
import Headtwo from "@/components/head/headtwo";
export default {
  props: {},
  data() {
    return {
      radio: "支付宝",
      show: false,
      noshow: false,
      paym: 100,
      allmoney: 0
    };
  },

  mounted() {
      let username=localStorage.getItem('username');
              this.$axios
            .get("http://192.168.0.112:8080/counts/yue?uname=" + username)
            .then((op)=>{
                 this.allmoney=op.data ;
                 this.allmoney = this.allmoney?this.allmoney:0;
            }).catch((err)=>{
                  console.log(err)
            });
   
    localStorage.setItem("paymoney", 100);
    let monli = document.getElementsByClassName("moneylist")[0];
    monli.onclick = function(e) {
      for (let i = 0; i < monli.children.length; i++) {
        monli.children[i].style.background = "#f0f0f0";
      }
      if (e.target.nodeName == "LI") {
        setTimeout(() => {
          e.target.style.background = "#ffe009";
          localStorage.setItem("paymoney", e.target.innerHTML - 0);
        }, 100);
      }

      // console.log(e.target.innerHTML);
    };
  },
  updated() {},
  methods: {
    chongzhifn() {
      // alert( localStorage.getItem('paymoney'));
      let username = localStorage.getItem("username");
      if (username) {
        let timer = null;
        this.show = true;
        clearInterval(timer);
        timer = setTimeout(() => {
          this.show = false;
          
          this.allmoney -= 0;
          // http://172.25.7.31:8080/counts/yue?uname=13526141931
          
          let paymoney= localStorage.getItem("paymoney") - 0;
          // localStorage.setItem('allmoney',this.allmoney);
          this.$axios.get("http://192.168.0.112:8080/counts/chongzhi?uname="+username+"&czmoney="+paymoney)
          .then((op)=>{
             if(op.data){
                this.$toast("支付成功");
                this.$axios.get('http://192.168.0.112:8080/counts/yue?uname='+username)
                .then(op=>{
                  localStorage.setItem('allmoney',op.data);
                  this.allmoney=op.data;
                }).catch(err=>{
                  console.log(err);
                })
             }else{
               this.$toast("支付失败");
             }
          })
          // alert(this.paymonry);
        }, 5000);
      }else{
        this.$toast("请先登录");
      }
    }
  },
  components: {
    Headtwo
  }
};
</script>

<style scoped lang="less">
.danxuan {
  position: absolute;
  top: 1.3rem;
  left: 5.5rem;
  .van-radio {
    margin-bottom: 0.62rem;
  }
}
.select .oneliqian {
  background: #ffe009;
}
.van-loading {
  width: 3rem;
  border: #ccc 0.02rem solid;
  padding: 0.3rem;
  border-radius: 0.1rem;
}
.chongzhi {
  text-align: left;
  background: #f0f0f0;
  height: 100%;
  padding-top: 0.88rem;
}
.my-balance {
  width: 6.9rem;
  height: 4.13rem;
  background: #fff;
  border-radius: 0.2rem;
  margin: 0.28rem auto;
}
.my-balance > div {
  display: flex;
  /*justify-content: space-between;*/
}
.my-balance > div > img {
  width: 0.57rem;
  height: 0.45rem;
}
.left {
  margin-left: 0.55rem;
  margin-top: 0.61rem;
}
.right {
  font-size: 0;
  margin-top: 0.55rem;
  margin-left: 0.1rem;
}
.right > span {
  font-size: 0.3rem;
}
.right > p {
  font-size: 0.24rem;
  color: #999999;
}
.select {
  margin-left: 0.7rem;
}
.select > ul {
  margin-top: 0.42rem;
  margin-left: 0rem;
  display: flex;
  flex-wrap: wrap;
}
.select > p {
  margin-top: 0.1rem;
  font-size: 0.3rem;
  position: absolute;
}
.select > ul > li {
  margin-top: 0.25rem;
  margin-left: 0.21rem;
  width: 1.12rem;
  height: 0.44rem;
  background: #f0f0f0;
  border-radius: 0.1rem;
  text-align: center;
  line-height: 0.44rem;
  font-size: 0.3rem;
}
.select > ul > li > a {
  color: #333333;
}
.way {
  /*padding: 0.41rem 0.5rem 0 0 ;*/
  position: relative;
  width: 6.9rem;
  height: 5.82rem;
  background: #fff;
  border-radius: 0.2rem;
  margin: 0rem auto;
  overflow: hidden;
}
.way > p {
  display: block;
  font-size: 0.3rem;
  margin-left: 0.57rem;
  margin-top: 0.36rem;
}
.way > ul {
  margin-top: 0.36rem;
  margin-left: 0.51rem;
}
.way > ul > li {
  position: relative;
  margin-top: 0.39rem;
  width: 100%;
  display: flex;
}
.way > ul > li > img {
  width: 0.66rem;
  height: 0.66rem;
}
.way > ul > li > .txt {
  /*line-height: 0.66rem;*/
  //   margin-left: 0.32rem;
  font-size: 0.3rem;
  position: absolute;
  top: 0.1rem;
  left: 0.8rem;
  /*margin-top: -0.1rem;*/
}
.img {
  /*margin-right: 1.68rem;*/
  position: absolute;
  right: -0.6rem;
}
.img > img {
  width: 0.44rem;
  height: 0.44rem;
  margin-right: 1.68rem;
}
.way > .btn {
  border-radius: 0.1rem;
  margin: 0.6rem auto;
  width: 5.31rem;
  height: 0.64rem;
  border: none;
  background: #f9e55b;
  text-align: center;
  line-height: 0.64rem;
  font-size: 0.2rem;
}
</style>



