<template>
 <div>
    <!--头部开始-->
    <header>
      <nav>
        <a><img src='./img2/fanhui_1@2x.png' @click='backfn'></a>
        <a>
          <p v-text='title'></p>
        </a>
        <a v-text='rgt' class="rgt"></a>
      </nav>
    </header>
 </div>
</template>

<script>
export default {
 props:['title','rgt'],
 data() {
 return {
   
 };
 },
 methods: {
    backfn() {
      this.$router.go(-1);
    }
 },
 components: {

 },
};
</script>

<style scoped lang="less">
header {
  padding: 0 0.12rem;
  width: 100%;
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  z-index:9999;
  background-color: #fff;
}
header > nav {
  padding: 0.14rem 0.3rem 0.12rem ;
  height: 0.88rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  .rgt{
    font-size: 0.24rem;
    color:#333;
  }
}
header > nav > a > img {
  width: 0.16rem;
  height: 0.27rem;
}
header > nav > a > p {
  font-family: "苹方粗体";
  font-size: 0.32rem;
  text-align: center;
  color:#333;
}
header > nav > a:nth-child(2) {
  text-align: center;
}
</style>



