<template>
 <div class="wai">
   <Headtwo title='钱包' rgt='明细'></Headtwo>
   <section>
       <div class="yue">
           <p>{{allmoney}}</p>
           <img src="./img/yue.png" alt="">
       </div>
       <img src="./img/img_1@2x.png" alt="" class="midimg">
       <ul class="list">
           <li>
                <div class="img"></div>
                <span><router-link to='/chongzhi'>充值</router-link></span>
                
           </li>
           <li>
                <div class="img"></div>
                <span>银行卡</span>
           </li>
           <li>
                <div class="img"></div>
                <span>充值记录</span>
           </li>
       </ul>
   </section>
 </div>
</template>

<script>
import Headtwo from '@/components/head/headtwo'
export default {
 props: {

 },
 data() {
 return {
    allmoney:0,
 };
 },
 mounted() {
    let username=localStorage.getItem('username');
              this.$axios
            .get("http://192.168.0.112:8080/counts/yue?uname=" + username)
            .then((op)=>{
                 this.allmoney=op.data ;
            }).catch((err)=>{
                  console.log(err)
            })
 },
 methods: {
 
 },
 components: {
 Headtwo
 },
};
</script>

<style scoped lang="less">

.wai{
    height: 100%;
}
  section{
      background: #f0f0f0;
      padding-top:1.18rem; 
      height: 100%;
      .yue{
          padding-top: 0.3rem;
          width: 6.9rem;
          height: 3.24rem;
          margin: 0 auto;
        //   background: url('../../../static/upload/yue.png') no-repeat center;
        //   background-size:6.9rem 3.24rem; 
          position: relative;
          img{
              width: 6.9rem;
              height: 3.24rem;
              position: relative;
              z-index:0;
              top: -0.3rem;
          }
          p{
              font-size: 0.48rem;
              text-align: center;
              position: absolute;
              left:0;
              bottom:0.3rem;
              width: 100%;
              z-index:1;
          }
      }

      .midimg{
          width: 6.9rem;
          height: 1.23rem;
          margin: 0.3rem auto;
      }
      .list{
         margin: 0.18rem 0.3rem 0;
         height: 2.8rem;
         padding-top: 0.18rem;
          background-color: #fff;
          border-radius: 0.1rem;
          a{
              color:#333;
          }
          li{
              height: 0.74rem;
              display: flex;
              align-items: center;
              .img{
                  width: 0.45rem;
                  height: 0.45rem;
                  margin: 0 0.47rem 0 0.38rem;
              }
              &:nth-child(1)>.img{
                  background: url('./img/tubiao_1@2x.png') no-repeat center;
                  background-size: 0.43rem 0.33rem; 
              }
               &:nth-child(2)>.img{
                  background: url('./img/tubiao_2@2x.png') no-repeat center;
                  background-size: 0.43rem 0.33rem; 
              }
               &:nth-child(3)>.img{
                  background: url('./img/tubaio_3@2x.png') no-repeat center;
                  background-size: 0.45rem 0.38rem; 
              }
              span{
                  font-size: 0.3rem;
                  color:#333;
              }
          }

      }
  }
</style>



