</template>

<script>
export default {
  props: {},
  data() {
    return {};
  },
  methods: {},
  components: {},
  beforeCreate() {
     this.$router.push({path:'/indexgen/indexone'})
  },
};
</script>

<style scoped lang="less">
.nav {
     position: fixed;
     top: 0.88rem;
     width: 100%;
     z-index:2;
  padding: 0 0.3rem;
  display: flex;
  align-items: center;
  background-color: #fff;
  li {
    flex: 1;
    height: 0.64rem;
    a {
      font-size: 0.28rem;
      color: #333;
      display: block;
      height: 100%;
      line-height: 0.64rem;
      border-bottom: #fff 0.04rem solid;
    }
    .router-link-active {
      border-bottom: #ffe009 0.04rem solid;
      font-size: 0.3rem;
    }
  }
}
</style>



