import Vue from 'vue'
import Router from 'vue-router'
import '@/assets/common.css';

Vue.use(Router)

import Indexgen from "@/components/indexgen";
// 首页四个2级路由
import Indexone from "@/components/indexone/indexone";
import Indextwo from "@/components/indextwo/indextwo";
import Indexthree from "@/components/indexthree/indexthree";
import Indexfrou from "@/components/indexfrou/indexfrou";

//Indexone中的3级路由
import Aodi from "@/components/indexone/luyou/aodi";
import Baoma from "@/components/indexone/luyou/baoma";
import Benchi from "@/components/indexone/luyou/benchi";
import Dazhong from "@/components/indexone/luyou/dazhong";
import Jiebao from "@/components/indexone/luyou/jiebao";
import More from "@/components/indexone/luyou/more";

// Indexone路由
import City from "@/components/indexone/city/city"

import Qianbao from "@/components/qianbao/qianbao"
import Chongzhi from "@/components/qianbao/chongzhi"

//登录
import Denglu from "@/components/denglu/denglu"
import Zhuce from "@/components/denglu/zhuce"

//客服
import Kefu from "@/components/kefu/kefu"

//订单
import Zijiadingdan from "@/components/dingdan/zijiadingdan"
import Xiangqing from "@/components/dingdan/xiangqing"

//门店
import Mendian from "@/components/mendian/mendian"
import Menxiang from "@/components/mendian/menxiang"

//车辆详情
import Chexiang from "@/components/chexiang/Chexiang"

//引导页
import Yindao from "@/components/yindaoye/yindao"

export default new Router({
  routes: [{
      path: '/',
      component: Indexgen
    },
    {
      path: '/indexgen',
      component: Indexgen,
      children: [{
        path: 'indexone',
        component: Indexone,
        children: [{
            path: '/',
            component: Dazhong
          },
          {
            path: 'aodi',
            component: Aodi
          },
          {
            path: 'baoma',
            component: Baoma
          },
          {
            path: 'benchi',
            component: Benchi
          },
          {
            path: 'dazhong',
            component: Dazhong
          },
          {
            path: 'jiebao',
            component: Jiebao
          },
          
        ]
      }, {
        path: 'indextwo',
        component: Indextwo
      }, {
        path: 'indexthree',
        component: Indexthree
      }, {
        path: 'indexfrou',
        component: Indexfrou
      }]
    },
    {
      path: '/city',
      component: City
    },
    {
      path: '/qianbao',
      component: Qianbao,
    },
    {
        path: '/chongzhi',
        component:Chongzhi
      },
      {
        path: '/more',
        component: More
      },
      {
        path: '/denglu',
        component: Denglu
      },
      {
        path: '/zhuce',
        component: Zhuce
      },
      {
        path: '/kefu',
        component: Kefu
      },
      {
        path: '/zijiadingdan',
        component:Zijiadingdan
      },
      {
        path: '/xiangqing',
        component:Xiangqing
      },
      {
        path: '/mendian',
        component:Mendian
      },
      {
        path: '/menxiang',
        component:Menxiang
      },
      {
        path: '/chexiang',
        component:Chexiang
      },
      {
        path: '/yindao',
        component:Yindao
      },
  ]
})



