<template>
 <div class="yindao">
  <van-swipe :autoplay="3000" indicator-color="white" :loop='false'>
  <van-swipe-item>
      <img src="./img/1.gif" alt="">
  </van-swipe-item>
  <van-swipe-item>
      <img src="./img/2.gif" alt="">
  </van-swipe-item>
  <van-swipe-item>
      <img src="./img/3.gif" alt="">
  </van-swipe-item>
  <van-swipe-item>
      <p class="tiyan" @click="tiyanfn"></p>
      <img src="./img/4.gif" alt="">
  </van-swipe-item>
</van-swipe>
 </div>
</template>

<script>
export default {
 props: {

 },
 data() {
 return {

 };
 },
 methods: {
   tiyanfn(){
      this.$router.push('/indexgen');
   }
 },
 components: {

 },
};
</script>

<style scoped lang="less">
.yindao{
    height: 100%;
    .van-swipe{
        height: 100%;
        img{
            width: 100%;
            height: 100%;

        }
        
    }
    .tiyan{
        position: absolute;
        left: 50%;
        bottom: 1.2rem;
        transform: translateX(-50%);
        width: 5rem;
        height: 1rem;
        // background-color: aqua;
    }
}
</style>



