<template>
  <div class="zhucebox">
    <header>
      <p>
        <router-link to="/zhuce">
          <img src="./img/fanhui_@2x.png" alt @click="fanhuifn()" />
        </router-link>
      </p>
    </header>
    <div class="img">
      <img src="./img/logo@2x.png" />
    </div>
    <div class="inputbox">
      <input type="text" placeholder="请输入手机号" class="tel" v-model="tel" @keyup="keyfn()" />
      <input type="text" placeholder="请输入验证码" class="pas" v-model="yannum" />
      <input type="password" placeholder="请输入密码（6-18数字）" class="pas2" v-model="pas" />
      <input type="button" class="btn" value="注册" @click="zhucefn" />
      <!-- <span class="num" v-text="yanbtn" @click="yanfn()" disabled></span> -->
      <input type="button" class="num" v-model="yanbtn" @click="yanfn()" />
    </div>
    <van-popup v-model="show" :style="{ height: '20%' }">
      <span class="errtitle" v-text="title"></span>
      <span class="dengluerr" v-text="errinfo"></span>
      <span class="yes">确定</span>
    </van-popup>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      show: false,
      tel: "",
      yannum: "",
      pas: "",
      errinfo: "",
      yanbtn: "获取验证码",
      sec: 30,
      title: "注册失败",
      duanxinnum: 0
    };
  },
  mounted() {},
  methods: {
    fanhuifn() {
      this.$router.go(-1);
    },
    keyfn() {
      let tel = this.tel-0;
      if (this.tel.length == 11) {
        this.$axios
          .get("http://192.168.0.112:8080/users/ajax?uname=" + tel)
          .then(op => {
            if (!op.data) {
              this.title = "您已注册";
              this.errinfo = "请您直接登录";
              this.show = true;
            }
          });
      }
    },
    zhucefn() {
      this.title = "注册失败";
      let tel = this.tel - 0;
      let pas = this.pas - 0;
      let yannum = this.yannum;
      let regpas = /^\d{6,18}$/;
      let regtel = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/;
      if (!regtel.test(tel)) {
        this.errinfo = "请输入正确的手机号";
        this.show = true;
      } else if (
        this.yannum - 0 != this.duanxinnum ||
        this.yannum.length != 4
      ) {
        this.errinfo = "您输入的验证码有误";
        this.show = true;
      } else if (!regpas.test(pas)) {
        this.errinfo = "请输入6-18位数字";
        this.show = true;
      } else {
        this.$axios
          .get(
            "http://192.168.0.112:8080/users/insert?uname=" + tel + "&pwd=" + pas
          )
          .then(op => {
            // console.log(op);
            if (op.data == "aaaaaa") {
              this.title = "恭喜您";
              this.errinfo = "您已注册成功，将跳往首页";
              localStorage.setItem('username',tel);
              // localStorage.setItem('userid',op.data.uid);
              this.show = true;
              setTimeout(() => {
                this.$router.push("/indexgen");
              }, 3000);
            } else if (op.data == "bbbbbb") {
              this.title = "您已注册";
              this.errinfo = "请您直接登录";
              this.show = true;
            } else {
              console.log(op);
            }
          })
          .catch(err => {
            console.log(err);
            this.title = "抱歉";
            this.errinfo = "注册失败，请再试一次";
            this.show = true;
          });
      }
    },

    yanfn() {
      let yanzhengma = document.getElementsByClassName("num")[0];
      yanzhengma.setAttribute("disabled", "true");

      //随机验证码
      let timer2 = null;
      clearTimeout(timer2);
      timer2 = setTimeout(() => {
        let suijinum = "";
        for (let i = 0; i < 4; i++) {
          suijinum += Math.floor(Math.random() * 10);
        }
        this.title = "短信提醒";
        this.errinfo = "您的验证码为" + suijinum;
        this.show = true;
        this.duanxinnum = suijinum - 0;
      }, 3000);

      let timer = null;
      clearInterval(timer);
      timer = setInterval(() => {
        this.yanbtn = "(" + this.sec + "s)后重新获取";
        this.sec--;
        if (this.sec <= -2) {
          clearInterval(timer);
          this.yanbtn = "获取验证码";
          this.sec = 30;
          yanzhengma.removeAttribute("disabled");
        }
      }, 1000);
    }
  },
  components: {}
};
</script>

<style scoped lang="less">
.van-popup--center {
  padding-top: 0.3rem;
  font-size: 0;
  border: #ccc solid 1px;
  height: 2.8rem;
  width: 5.4rem;
  border-radius: 0.16rem;
  span {
    display: block;
  }
  .errtitle {
    font-size: 0.48rem;
  }
  .dengluerr {
    font-size: 0.3rem;
    padding: 0.2rem 0 0.3rem;
    width: 4.5rem;
    border-bottom: rgb(204, 204, 204) solid 0.02rem;
    margin: 0 auto;
  }
  .yes {
    font-size: 0.3rem;
    margin-top: 0.1rem;
    color: rgb(243, 241, 99);
  }
}

.zhucebox {
  .dengluerr {
    font-size: 0.3rem;
  }
  height: 100%;
  background: url("./img/背景@2x.png") no-repeat;
  background-size: 7.5rem auto;
  .img {
    margin-top: 1.2rem;
    img {
      width: 2.67rem;
      height: 2.21rem;
      margin: 0 auto;
    }
  }
}
header {
  overflow: hidden;
  text-align: right;
  font-size: 0.3rem;
  color: #515151;
  p {
    padding-right: 0.4rem;
    height: 0.88rem;
    line-height: 0.88rem;
    margin-top: 0.2rem;
    display: flex;
    align-items: center;
    a {
      img {
        width: 0.16rem;
        height: 0.27rem;
        margin-left: 0.3rem;
      }
    }
  }
}
.inputbox {
  font-size: 0;
  margin-top: 2.6rem;
  position: relative;
  .tel {
    width: 5.62rem;
    height: 0.78rem;
    font-size: 0.24rem;
    border: #515151 0.03rem solid;
    border-radius: 0.39rem;
    text-indent: 0.6rem;
  }
  .pas,
  .pas2 {
    width: 5.62rem;
    height: 0.78rem;
    font-size: 0.24rem;
    border: rgb(51, 51, 51) 0.03rem solid;
    border-radius: 0.39rem;
    text-indent: 0.6rem;
    margin-top: 0.5rem;
  }
  .btn {
    width: 5.62rem;
    height: 0.78rem;
    font-size: 0.36rem;
    border-radius: 0.39rem;
    color: #555;
    background-color: rgb(255, 253, 119);
    border: none;
    outline: none;
    margin-top: 1rem;
  }
  .num {
    position: absolute;
    top: 1.5rem;
    right: 1.2rem;
    font-size: 0.24rem;
    background: #fff;
    border: none;
  }
}
</style>



