<template>
  <div class="headerbox">
    <div class="img left">
      <img src="./img/icon1@2x.png" @click="showPopup" />
    </div>
    <p>平驾租车</p>
    <div class="img right">
      <router-link to="/kefu">
        <img src="./img/kefu_1@2x.png" />
      </router-link>
    </div>

    <!-- //侧边栏 -->
    <van-popup v-model="show" position="left" :style="{ height: '100%',width:'74.3%' }">
      <div class="cebian">
        <div class="header">
          <div class="headercon">
            <div class="img">
              <img src="./img/touxiang_1@2x.png" alt />
            </div>
            <div class="right">
              <p v-text='username' @click="tiaodenglufn()">平驾小U</p>
              <div class="sbox">
                <div class="span1">普通用户</div>
                <div class="span1">132积分</div>
              </div>
            </div>
          </div>
        </div>
        <div class="navone">
          <div class="one">
            <img src="./img/jingangqu_1@2x.png" alt />
            <span>一键约车</span>
          </div>
          <div class="one">
            <img src="./img/jingangqu_2@2x.png" alt />
            <span>会员</span>
          </div>
          <div class="one">
            <img src="./img/jingangqu_3@2x.png" alt />
            <span>我的信用</span>
          </div>
        </div>
        <ul class="navtwo">
          <li>
            <div class="img"></div>
            <span>
              <router-link to="/qianbao">我的钱包</router-link>
            </span>
          </li>
          <li>
            <div class="img"></div>
            <span>我的优惠券</span>
          </li>
          <li>
            <div class="img"></div>
            <span>
              <router-link to="/zijiadingdan">我的订单</router-link>
            </span>
          </li>
          <li>
            <div class="img"></div>
            <span>积分商城</span>
          </li>
          <li>
            <div class="img"></div>
            <span>我的用户反馈</span>
          </li>
          <li>
            <div class="img"></div>
            <span>检测更新</span>
          </li>
        </ul>
        <div class="bottom">
          <div class="left2">
            <img src="./img/sheizhi_1@2x.png" alt />
            <span>设置</span>
          </div>
          <div class="left2" @click="tuichufn()">
            <img src="./img/tuichu_1@2x.png" alt />
            <span>
              退出
            </span>
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import Vue from "vue";
import Vant from "vant";
import "vant/lib/index.css";
Vue.use(Vant);

import { Popup } from "vant";

Vue.use(Popup);

export default {
  props: {},
  data() {
    return {
      show: false,
      username:'请登录',
    } 
  },
  mounted() {
     
  },
  methods: {
    showPopup() {
      let usertext=localStorage.getItem('username');
      // console.log(usertext);
      if(usertext){
        this.username=usertext;
      }
      this.show = true;
    },
    tuichufn(){
      localStorage.removeItem('username');
      this.username='请登录';
      this.$notify('您已退出登录');
      setTimeout(() => {
         this.$router.push('/yindao');
      }, 1000);
    },
    tiaodenglufn(){
        if(this.username=='请登录'){
          this.$router.push('/denglu');
        }
    },
  },
  components: {}
};
</script>

<style scoped lang="less">
.headerbox {
  position: fixed;
  width: 100%;
  z-index: 99;
  display: flex;
  height: 0.88rem;
  align-items: center;
  background-color: #fff;
  a {
    color: #333;
    display: block;
    width: 100%;
    height: 100%;
  }
  .left {
    width: 0.38rem;
    height: 0.41rem;
    margin-left: 0.3rem;
  }
  .right {
    width: 0.43rem;
    height: 0.44rem;
    margin-right: 0.3rem;
  }
  p {
    font-size: 0.32rem;
    flex: 1;
    color: #333;
  }
  img {
    width: 100%;
    height: auto;
    display: block;
  }

  .cebian {
    width: 100%;
    height: 100%;
    .header {
      width: 100%;
      height: 3.56rem;
      background-color: #f9da60;
      overflow: hidden;
      .headercon {
        margin-top: 1.26rem;
        margin-left: 0.54rem;
        display: flex;
        .img {
          width: 1.22rem;
          height: 1.22rem;
          border-radius: 50%;
          overflow: hidden;
          img {
            width: 1.22rem;
            height: 1.22rem;
          }
        }
        .right {
          width: 70%;
          text-align: left;
          padding-top: 0.2rem;
          padding-left: 0.4rem;
          p {
            margin-bottom: 0.2rem;
          }
          .sbox {
            font-size: 0;
            display: flex;
            .span1 {
              font-size: 0.2rem;
              color: #333;
              // width: 1.1rem;
              // height: 0.38rem;
              margin-right: 0.16rem;
              border: #666 solid 0.02rem;
              border-radius: 0.1rem;
              padding: 0.02rem 0.08rem;
            }
          }
        }
      }
    }

    .navone {
      padding: 0.5rem 0.3rem;
      display: flex;
      font-size: 0.26rem;
      color: #333;
      justify-content: space-between;
      .one {
        img {
          width: 0.65rem;
          height: 0.65rem;
          margin: 0 auto 0.2rem;
        }
      }
    }

    .navtwo {
      margin-top: 0.22rem;
      padding: 0 0.54rem;
      li {
        height: 0.78rem;
        display: flex;
        align-items: center;
        font-size: 0.26rem;
        color: #333;
        .img {
          width: 0.5rem;
          height: 0.5rem;
          margin-right: 0.5rem;
        }
        &:nth-child(1) > .img {
          background: url("./img/tubiao_1@2x.png") no-repeat center;
          background-size: 0.47rem auto;
        }
        &:nth-child(2) > .img {
          background: url("./img/tubiao_2@2x.png") no-repeat center;
          background-size: 0.45rem auto;
        }
        &:nth-child(3) > .img {
          background: url("./img/tubiao_3@2x.png") no-repeat center;
          background-size: 0.34rem auto;
        }
        &:nth-child(4) > .img {
          background: url("./img/tubiao_4@2x.png") no-repeat center;
          background-size: 0.47rem auto;
        }
        &:nth-child(5) > .img {
          background: url("./img/tubiao_5@2x.png") no-repeat center;
          background-size: 0.36rem auto;
        }
        &:nth-child(6) > .img {
          background: url("./img/tubiao_6@2x.png") no-repeat center;
          background-size: 0.44rem auto;
        }
      }
    }

    .bottom {
      height: 0.79rem;
      font-size: 0.26rem;
      color: #333;
      display: flex;
      position: absolute;
      bottom: 0.4rem;
      left: 0rem;

      .left2 {
        margin-left: 0.58rem;
        img {
          width: 0.33rem;
          margin: 0 auto 0.2rem;
        }
      }
    }
  }
}
</style>



