<template>
  <div class="kefubox">
    <Headtwo title="客服"></Headtwo>
    <div class="bigbox">
      <div id="dongbox">
        <dl class="liaotianlist" v-for="(item , index) of kefustr" :key="index">
          <dt v-if="item.jshow">
            <div class="box">
              <img src="./img/touxiang1@2x.png" alt />
              <div class="info">
                <div class="kefuname">栀子花</div>
                <div class="text">
                  <p v-text="item.jiqi"></p>
                  <span>已读</span>
                </div>
              </div>
            </div>
          </dt>
          <dd v-if="item.mshow">
            <div class="box">
              <div class="info">
                <div class="text">
                  <p v-text="item.me">收到韩国风格都是对方回复感到十分广泛大使馆反对石帆胜丰士大夫官方的说法跟第三方</p>
                </div>
              </div>
              <img src="./img/touxiang2@2x.png" />
            </div>
          </dd>
        </dl>
      </div>
    </div>

    <p class="botbox">
      <input type="text" v-model="msg" />
      <span @click="sibfn()">发送</span>
    </p>
  </div>
</template>

<script>
import Headtwo from "@/components/head/headtwo";
export default {
  props: {},
  data() {
    return {
      msg: "",
      winh: 0,
      boxh: 0,
      kefustr: [
        {
          jiqi: "您好，请问您有什么需要帮助的？",
          jshow: true,
          me: "",
          mshow: false
        }
        // {
        //   jiqi: "",
        //   jshow: false,
        //   me: "hahahah",
        //   mshow: true
        // }
      ]
    };
  },
  mounted() {
    this.winh = document.body.clientHeight;
    this.boxh = document.getElementById('dongbox').offsetHeight;
  },
  methods: {
    sibfn() {
      if (this.msg) {
        this.kefustr.push({
          jiqi: "",
          jshow: false,
          me: this.msg,
          mshow: true
        });
        let jiqiarr = [];
        for (let i = 0; i < this.kefustr.length; i++) {
          if (this.kefustr[i].jiqi != "") {
            jiqiarr.push(this.kefustr[i].jiqi);
          }
        }
        let jiqilast = jiqiarr[jiqiarr.length - 1];
        // console.log(jiqilast);

        let timer = null;
        timer = setInterval(() => {
          this.$axios
            .get("http://192.168.0.112:8080/users/over")
            .then(op => {
              if (op.data.reply != jiqilast) {
                // console.log(op);
                this.kefustr.push({
                  jiqi: op.data.reply,
                  jshow: true,
                  me: "",
                  mshow: false
                });
                clearInterval(timer);
              }
            })
            .catch(err => {
              console.log(err);
            });
        }, 1000);
      }
    }
  },
  updated() {
    let box=document.getElementById('dongbox')
    this.boxh = box.offsetHeight;
    //  console.log(this.boxh,this.winh);
     if(this.boxh>575){
        box.style.bottom=0;
     }
  },
  components: {
    Headtwo
  }
};
</script>

<style scoped lang="less">


.bigbox{
  // max-height: 12rem;
  height: 12rem;
  overflow-y: auto;
  position: relative;
  z-index:2;
  margin: 0 0.3rem;
}
#dongbox{
  width: 100%;
  // width: 6.9rem;
  position: absolute;
  left: 0;
  // top: 0;
}
.kefubox {
  padding-top: 0.88rem;
  font-size: 0.26rem;
  height: 100%;
  .botbox {
    position: fixed;
    z-index:8;
    bottom: 0rem;
    left: 50%;
    transform: translateX(-50%);
    height: 1.2rem;
    width: 6.9rem;
    background: #fff;
    display: flex;
    align-items: center;
    input {
      height: 0.7rem;
      width: 5.6rem;
      border: 0.02rem #999 solid;
      border-radius: 0.1rem;
      text-indent: 0.1rem;
    }
    span {
      height: 0.66rem;
      line-height: 0.66rem;
      text-align: center;
      background: rgb(255, 226, 9);
      display: block;
      flex: 1;
      border-radius: 0.1rem;
      margin-left: 0.1rem;
    }
  }
  #dongbox{
    margin: 0.3rem 0;
    dt,
    dd {
      margin-bottom: 0.78rem;
    }
    dt {
      img {
        width: 1.08rem;
        height: 1.08rem;
        border-radius: 50%;
      }
      .box {
        display: flex;
        font-size: 0.22rem;
        text-align: left;
        .info {
          margin-left: 0.18rem;
          .kefuname {
            margin-bottom: 0.18rem;
          }
          .text {
            display: flex;
            position: relative;
            p {
              font-size: 0.28rem;
              width: 3.62rem;
              padding: 0.26rem 0.2rem;
              background: rgb(243, 243, 243);
              border-radius: 0.1rem;
            }
            span {
              position: absolute;
              bottom: 0;
              right: -1rem;
            }
          }
        }
      }
    }

    dd {
      img {
        width: 1.08rem;
        height: 1.08rem;
        border-radius: 50%;
      }
      .box {
        display: flex;
        font-size: 0.22rem;
        text-align: left;
        justify-content: flex-end;
        .info {
          margin-right: 0.18rem;

          .text {
            display: flex;
            position: relative;
            p {
              font-size: 0.28rem;
              width: 3.62rem;
              padding: 0.26rem 0.2rem;
              background: rgb(255, 226, 9);
              border-radius: 0.1rem;
            }
            span {
              position: absolute;
              bottom: 0;
              right: -1rem;
            }
          }
        }
      }
    }
  }
}
</style>



