<template>
  <div class="chexiangbox">
    <Headtwo title="车辆详情"></Headtwo>

    <div class="box">
      <!-- <img :src="./img/chepic.png" alt /> -->
      <img :src="imgsrc" alt />
      <ul>
         <li><h1>{{carname}}</h1></li>
         <li><span class="price">{{carprice}}</span>/小时</li>
         <li><van-tag >SUV</van-tag><van-tag >2.7自动</van-tag><van-tag >乘坐5人</van-tag></li>
         <li>续航250公里</li>
         <li>车身颜色：银色，黑色，白色</li>
         <li>车辆类型：舒适型，尊贵型</li>
         <li><hr></li>
         <li>
           品牌
           <p class="one">{{carname}}</p>
         </li>
         <li>
           续航里程
           <p class="one">250公里</p>
         </li>
         <li>
           动力类型
           <p class="one">伪电动</p>
         </li>
         <li>
           电池类型
           <p class="one">三雄电动</p>
         </li>
         <li>
           车身颜色
           <p class="one">银色</p>
         </li>
         <li>
           充电类型
           <p class="one">快充</p>
         </li>
         <li>
           百公里耗油
           <p class="one">10L</p>
         </li>
         <li>
           轮速
           <p class="one">1502/1492</p>
         </li>
         <li>
           最小离地距离
           <p class="one">115</p>
         </li>  
      </ul>
      <input type="button" value='立即租车' class='btn' @click="fn()">
    </div>
  </div>
</template>

<script>
import Headtwo from "@/components/head/headtwo";
export default {
  props: {},
  data() {
    return {
      carname:'丰田汉兰达',
      carprice:68,
      imgsrc:'./img/chepic.png',
    };
  },
  mounted() {
    let cararr=JSON.parse(localStorage.getItem('nowlodingcar'));
    console.log(cararr);
    this.carname=cararr.ctype+cararr.cname;
    this.carprice=cararr.h_price;
    this.imgsrc=cararr.images;
  },
  methods: {
    fn(){
      this.$router.push('/xiangqing');
      
    }
  },
  components: {
    Headtwo
  }
};
</script>

<style scoped lang="less">
.chexiangbox {
  padding-top: 0.88rem;
  height: 100%;
  background: #f0f0f0;
  text-align: left;
  .price{
    color:red;
  }
  .box {
    margin: 0.3rem 0.3rem 0;
    background: #fff;
    border-radius: 0.1rem;
    padding: 0.16rem 0.56rem 0;
    & > img {
      width: 4.38rem;
      height: 2.24rem;
      margin: 0 auto;
      
    }
    ul{
      margin-top: 0.6rem;
      li{
        font-size: 0.24rem;
        margin-top: 0.16rem;
        color:rgb(153, 153, 153);
        position: relative;

        h1{
          font-size: 0.28rem;
          color:rgb(51, 51, 51);
        }
       .price{
          font-size: 0.34rem;
       } 
       .van-tag{
         margin-right: 0.06rem;
         background-color: #ccc;
       }
       .one{
         position: absolute;
         left: 2.6rem;
         top: 0rem;
       }
      }

    }
    .btn{
      width: 100%;
      font-size: 0.24rem;
      color:rgb(51, 51, 51);
      background-color: #F8E45A;
      height: 0.6rem;
      border:none;
      outline: none;
      border-radius: 0.1rem;
      position: relative;
      top:-0.2rem;
    }
  }
}
</style>



