<template>
  <div class="zhucebox">
    <header>
      <p>
        <router-link to="/zhuce">注册</router-link>
      </p>
    </header>
    <div class="img">
      <img src="./img/logo@2x.png" alt />
    </div>
    <div class="inputbox">
      <input type="text" placeholder="请输入手机号" class="tel" v-model="tel" />
      <input type="password" placeholder="请输入密码" class="pas" v-model="pas" />
      <input type="button" class="btn" value="登录" @click="submi" />
    </div>
    <van-popup v-model="show" position="center" :style="{ height: '5.60rem',height:'2.6rem' }">
      <span class="errtitle" v-text="title"></span>
      <br />
      <span class="dengluerr" v-text="errinfo"></span>
      <span class="yes">确定</span>
    </van-popup>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      tel: "",
      pas: "",
      show: false,
      errinfo: "",
      title: "登录失败"
    };
  },
  methods: {
    // 点击登录
    submi() {
      this.title = "登录失败";
      let num = this.tel - 0;
      let pas = this.pas - 0;
      let reg = /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/;
      if (reg.test(num)) {
        this.$axios
          .get(
            "http://192.168.0.112:8080/users/logins?uname=" + num + "&pwd=" + pas
          )
          .then(op => {
            if (op.data === "nothisuser") {
              this.errinfo = "您还未注册";
              this.show = true;
            } else if (op.data === "errpassword") {
              this.errinfo = "密码错误";
              this.show = true;
            } else if (num == op.data.uname && pas == op.data.pwd) {
              this.title = "恭喜您";
              this.errinfo = "您已登录成功，将跳往首页";
              localStorage.setItem('username',num);
              // localStorage.setItem('userid',op.data.uid);
              this.show = true;
               setTimeout(()=>{
                this.$router.push('/indexgen');
              },3000)
            } else {
               this.title = "警告";
               this.errinfo = "未知错误，请重试";
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        this.errinfo = "请输入正确的手机号";
        this.show = true;
      }
    }

    // showPopup() {
    // this.show = true;
    // }
  },
  components: {}
};
</script>

<style scoped lang="less">
a {
  color: #333;
}

.van-popup--center {
  padding-top: 0.3rem;
  font-size: 0;
  border: #ccc solid 1px;
  height: 2.8rem;
  width: 5.4rem;
  border-radius: 0.16rem;
  span {
    display: block;
  }
  .errtitle {
    font-size: 0.36rem;
  }
  .dengluerr {
    font-size: 0.24rem;
    padding: 0.2rem 0 0.3rem;
    width: 4.5rem;
    border-bottom: rgb(204, 204, 204) solid 0.02rem;
    margin: 0 auto;
  }
  .yes {
    font-size: 0.3rem;
    margin-top: 0.2rem;
    color: rgb(233, 229, 27);
  }
}
.van-overlay {
  background: rgb(238, 42, 42);
}
.zhucebox {
  height: 100%;
  background: url("./img/背景@2x.png") no-repeat;
  background-size: 7.5rem auto;
  .img {
    margin-top: 1.2rem;
    img {
      width: 2.67rem;
      height: 2.21rem;
      margin: 0 auto;
    }
  }
}
header {
  overflow: hidden;
  text-align: right;
  font-size: 0.3rem;
  color: #515151;
  p {
    padding-right: 0.4rem;
    height: 0.88rem;
    line-height: 0.88rem;
    margin-top: 0.2rem;
  }
}
.inputbox {
  font-size: 0;
  margin-top: 2.6rem;
  .tel {
    width: 5.62rem;
    height: 0.78rem;
    font-size: 0.24rem;
    border: #515151 0.03rem solid;
    border-radius: 0.39rem;
    text-indent: 0.6rem;
  }
  .pas {
    width: 5.62rem;
    height: 0.78rem;
    font-size: 0.24rem;
    border: rgb(51, 51, 51) 0.03rem solid;
    border-radius: 0.39rem;
    text-indent: 0.6rem;
    margin-top: 0.5rem;
  }
  .btn {
    width: 5.62rem;
    height: 0.78rem;
    font-size: 0.36rem;
    border-radius: 0.39rem;
    color: #555;
    background-color: rgb(255, 253, 119);
    border: none;
    outline: none;
    margin-top: 1.2rem;
  }
}
</style>



